import { makeStyles } from '@mui/styles';
import React from 'react';
import { Container, Grid, Typography } from '@mui/material';
import Forms from '../Forms';
import { useLocation, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { updateSnackbarState } from '../../reducer/commonSlice';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexFlow: 'column',
    height: '100vh',
    width: '100vw',
    paddingTop: '10vh',
    boxSizing: 'border-box',
  },
  formDiv: { overflowY: 'scroll', padding: '1rem' },
});

const CreateGig = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  const { isLoggedIn } = useSelector(
    (state) => state?.commonReducer?.authState
  );
  const { state } = useLocation();
  const dispatch = useDispatch();
  React.useEffect(() => {
    if (!isLoggedIn) {
      dispatch(
        updateSnackbarState({
          open: true,
          message: 'Please Login First',
          type: 'error',
        })
      );
      navigate('/login');
    }
  }, []);
  return (
    <div className={classes.root}>
      {!isLoggedIn ? (
        <Typography>Please Login First</Typography>
      ) : (
        <Container className={classes.formDiv}>
          <Grid container alignItems='center' justifyContent='center'>
            <Forms
              type='create-gig'
              name='Create Gig'
              preDefinedValues={state}
            />
          </Grid>
        </Container>
      )}
    </div>
  );
};

export default CreateGig;
